import { Component, OnInit } from '@angular/core';
import { MyMatchesService } from "./myMatches.service";
import { StateServiceService } from "./../../state-service.service";
@Component({
  selector: 'app-myMatches',
  templateUrl: './myMatches.component.html',
  styleUrls: ['./myMatches.component.css']
})
export class MyMatchesComponent implements OnInit {
  uid: any;
  user: any;
  propertyDetails = [];
  propertyBuyer = [];
  matchedProperties: any;
  property: any;
  buyerProperty: any;
  UserId: any;
  constructor(
    private MatchesService: MyMatchesService,
    private stateService: StateServiceService
  ) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;

  // Fetch details Seller
  this.MatchesService.getMatchesSellerProperties(this.uid).then(
    res => {
      res.forEach(element => {
        this.property = element.data();
      });
    }
  );

  // Fetch details Seller
  this.MatchesService.getMatchesBuyerProperties(this.uid).then(
    res => {
      res.forEach(element => {
        this.buyerProperty=element.data();
      });
    }
  );
    
  
}


}