import { Component, OnInit } from "@angular/core";
import { AngularFireAuth } from "@angular/fire/auth";
import {AngularFirestore,AngularFirestoreDocument} from "@angular/fire/firestore";
import { listingSeller } from "../../../Model/listingSeller";
import { AuthService } from "../../../auth.service";
import { FormControl, Validators, FormGroup } from "@angular/forms";
import { VERSION,MatDialogRef,MatDialog,MatSnackBar,MAT_DIALOG_DATA} from "@angular/material";
import { StateServiceService } from "./../../../state-service.service";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { Observable } from "rxjs";
import { map, startWith } from "rxjs/operators";

import { Config, HttpService } from "./../../../http.service";
import { debounceTime } from "rxjs/operators";
import { switchMap } from "rxjs/operators";
import { of } from "rxjs";
import { NativeDateAdapter, DateAdapter, MAT_DATE_FORMATS } from "@angular/material";
import { AppDateAdapter, APP_DATE_FORMATS} from './../../../Helper/date.adapter';
import {AltertFormDialogComponent} from '../../../Misc/alertFormdialog/alertFormdialog.component';

declare var $: any;

@Component({
  selector: "app-fillFormSeller",
  templateUrl: "./fillFormSeller.component.html",
  styleUrls: ["./fillFormSeller.component.css"],
  providers: [
    {
        provide: DateAdapter, useClass: AppDateAdapter
    },
    {
        provide: MAT_DATE_FORMATS, useValue: APP_DATE_FORMATS
    }
    ]
})

export class  FillFormSellerComponent implements OnInit {
  public addressianAutoCompleteLooking$: Observable<any> = null;
  public autoCompleteControlLooking = new FormControl();
  public addressianAutoCompleteCurrent$: Observable<any> = null;
  public autoCompleteControlCurrent = new FormControl();
  data: any;
  isLoggedIn: Boolean = true;
  userData: any;
  config: Config;
  listingSeller: listingSeller = new listingSeller();
  submitted = false;
  isLoading: boolean = false;
  return: any;
  version = VERSION;
  ConfirmDetailService: any;
  postcodeFormControl = new FormControl("");
  form: FormGroup;
  signatureFormGroup: any;
  result: any;
  selectedIndex = 0; 
  maxNumberOfTabs = 2;
  myControl = new FormControl();
  features = new FormControl();
  applicable: string[] = ['Terrace House', 'Driveway', 'Period features','Garage','Wheelchair friendly','Gated community','External Rear Garden'];
  user: any;
  useremail: any;

  constructor(
    public authService: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    public afAuth: AngularFireAuth,
    private router: Router,
    private stateService: StateServiceService,
    private sellerService: HttpService,
    private dialog: MatDialog,
   
  ) {}

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    console.log(this.user);

    if (this.user) {
      //
    }
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        JSON.parse(localStorage.getItem("user"));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        JSON.parse(localStorage.getItem("user"));
        this.LoggedOut();
      }
    });

 // The auto population of github method
 this.addressianAutoCompleteLooking$ = this.autoCompleteControlLooking.valueChanges.pipe(
  startWith(""),
  // delay emits
  debounceTime(1000),
  // use switch map so as to cancel previous subscribed events, before creating new once
  switchMap(value => {
    if (value !== "") {
      this.lookup(this.listingSeller.Lookingpostcode).subscribe(data => {
        this.data = data;
      });

      return this.lookup(this.listingSeller.Lookingpostcode);
    } else {
      return of(null);
    }
  })
);
// End method
this.addressianAutoCompleteCurrent$ = this.autoCompleteControlCurrent.valueChanges.pipe(
  startWith(""),
  // delay emits
  debounceTime(1000),
  // use switch map so as to cancel previous subscribed events, before creating new once
  switchMap(value => {
    if (value !== "") {
      this.lookup(this.listingSeller.Currentpostcode).subscribe(data => {
        this.data = data;
      });

      return this.lookup(this.listingSeller.Currentpostcode);
    } else {
      return of(null);
    }
  })
);




 }
  private LoggedIn() {
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }
  newCustomer(): void {
    this.submitted = false;
    this.listingSeller = new listingSeller();
  }

  onSubmit() {
    // Get Seller variable into local scope for html
    this.stateService.listingSeller = this.listingSeller;
    this.router.navigate(["confirmSellerdetail"]);
  }

  FirstnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);
  LastnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);
  EmailFormControl = new FormControl("", [
    Validators.required,
    Validators.email
  ]);
  AddressFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);
  TownFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);
  stateFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  addressnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(6)
  ]);
  noroomsFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(2)
  ]);
  DOBFormControl = new FormControl("", [Validators.required]);

  lookup(value): Observable<any> {
    return this.sellerService.search(value);
  }
  getPosts(value) {
   this.listingSeller.CurrentAddress = value.address;
   this.listingSeller.CurrentTown = value.citytown;
   this.listingSeller.Currentstate = value.county;
   this.listingSeller.Currentpostcode = value.postcode;
    console.log(value);
  }
  getPost(value) {
   this.listingSeller.LookingTown = value.citytown;
   this.listingSeller.Lookingstate = value.county;
   this.listingSeller.Lookingpostcode = value.postcode;
   this.listingSeller.LookingAddress=value.address;
    console.log(value);
  } 
  getSame(listingSeller) {
   this.listingSeller.LookingTown = this.listingSeller.CurrentTown;
   this.listingSeller.Lookingstate = this.listingSeller.Currentstate;
   this.listingSeller.Lookingpostcode = this.listingSeller.Currentpostcode;
   this.listingSeller.LookingStreetname = this.listingSeller.CurrentTown;
   this.listingSeller.LookingAddress = this.listingSeller.CurrentAddress;
    console.log(listingSeller);
  }


  
  selectTab(nextIndex: number, presentIndex: number): void {

  
    if (presentIndex == 0) {    
      if (this.listingSeller.Title == null ||this.listingSeller.Firstname == null ||this.listingSeller.Lastname == null ||this.listingSeller.DOB == null ||this.listingSeller.phonenumber == null) {
        console.log("New Error");
        this.openAlertDialog() 
      }
      else 
      {
        this.selectedIndex = nextIndex;      
      }

    } 
    else if (presentIndex == 1) {
      if(nextIndex > presentIndex) {
         if (this.listingSeller.Currentpostcode == null || this.listingSeller.CurrentAddress == null ||  this.listingSeller.CurrentTown  == null ||this.listingSeller.CurrentTown == null ||this.listingSeller.Currentstate == null ||this.listingSeller.Lookingpostcode == null ||this.listingSeller.LookingAddress == null ||this.listingSeller.LookingTown == null ||this.listingSeller.Lookingstate == null ||this.listingSeller.PropertyType == null || this.listingSeller.norooms == null ||this.listingSeller.MaxAmount == null||this.listingSeller.MinAmount == null||this.listingSeller.nobathrooms== null||this.listingSeller.noreception == null||this.listingSeller.PropertyCondition == null||this.listingSeller.ownership == null||this.listingSeller.features == null ) {
        this.openAlertDialog() 
      }
      else {
        this.selectedIndex = nextIndex;
      }

      } else {
        this.selectedIndex = nextIndex;
      }     
    } 
    else if (presentIndex == 2) {
     

      if(nextIndex > presentIndex) {
        if (this.listingSeller.otherInfo == null) {
       this.openAlertDialog() 
     }
     else {
       this.selectedIndex = nextIndex;
     }

     } else {
       this.selectedIndex = nextIndex;
     }

    }
  
  
  }
  openAlertDialog() {
    const dialogRef = this.dialog.open(AltertFormDialogComponent,{
      data:{
        message: 'HelloWorld',
        buttonText: {
          cancel: 'Done'
        }
      },
    });
  }
}
