import {
  Component,
  ViewChild,
  AfterViewInit,
  OnDestroy,
  OnInit,
  ValueProvider
} from "@angular/core";
import { AuthService } from "./../../../auth.service";
import { listingBuyer } from "../../../Model/listingBuyer";
import { FormsService } from "./fillFormBuyer.service";
import { AngularFireAuth } from "@angular/fire/auth";
import { FormControl, Validators, FormGroup } from "@angular/forms";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
  MatAutocompleteSelectedEvent,
  MatAutocompleteTrigger
} from "@angular/material";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { StateServiceService } from "../../../state-service.service";
import { Observable, onErrorResumeNext } from "rxjs";
import { map, startWith, take } from "rxjs/operators";
import {
  AngularFirestore,
  AngularFirestoreDocument
} from "@angular/fire/firestore";
import { Config, HttpService } from "../../../http.service";
declare var $: any;
import { debounceTime } from "rxjs/operators";
import { switchMap } from "rxjs/operators";
import { of } from "rxjs";
import { NgModel } from "@angular/forms";
import { FormArray, FormBuilder } from "@angular/forms";
import {
  NativeDateAdapter,
  DateAdapter,
  MAT_DATE_FORMATS
} from "@angular/material";
import { AppDateAdapter, APP_DATE_FORMATS } from "./../../../Helper/date.adapter";
import {AltertFormDialogComponent} from '../../../Misc/alertFormdialog/alertFormdialog.component';
@Component({
  selector: "app-fillFormBuyer",
  templateUrl: "./fillFormBuyer.component.html",
  styleUrls: ["./fillFormBuyer.component.css"],
  providers: [
    {
      provide: DateAdapter,
      useClass: AppDateAdapter
    },
    {
      provide: MAT_DATE_FORMATS,
      useValue: APP_DATE_FORMATS
    }
  ]
})
export class FillFormBuyerComponent implements OnInit {
  public addressianAutoCompleteCurrent$: Observable<any> = null;
  public autoCompleteControlCurrent = new FormControl();
  public addressianAutoCompleteLooking$: Observable<any> = null;
  public autoCompleteControlLooking = new FormControl();
  data: any;
  isLoggedIn: Boolean = true;
  userData: any;
  config: Config;
  listingBuyer: listingBuyer = new listingBuyer();
  submitted = false;
  DatafieldsService: any;
  financial: any[];
  links = ["First", "Second", "Third"];
  activeLink = this.links[0];
  background = "";
  selectedIndex = 0;
  maxNumberOfTabs = 2;
  isLoading: boolean = false;
  return: any;
  obj: any;
  version = VERSION;
  form: FormGroup;
  signatureFormGroup: any;
  myControl = new FormControl();
  result: any;
  interestFormGroup: FormGroup;
  interests: any;
  selected: any;
  $scope: any;
  years: any[];
  selectedYears: any[];
  Condition = new FormControl();
  ConditionsList: string[] = [
    "Refurbished",
    "New build",
    "Needs modernisation"
  ];
  registerForm: FormGroup;
  user: any;
  useremail: any;





  toggleBackground() {
    this.background = this.background ? "" : "primary";
  }

  addLink() {
    this.links.push(`Link ${this.links.length + 1}`);
  }

  constructor(
    public authService: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    private formsService: FormsService,
    private firestore: AngularFirestore,
    public afAuth: AngularFireAuth,
    db: AngularFirestore,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private route: ActivatedRoute,
    private router: Router,
    private stateService: StateServiceService,
    private postcodeService: HttpService,
    private formBuilder: FormBuilder,
    public fb: FormBuilder,
 
  ) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    console.log(this.user);

    if (this.user) {
      //
    }
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        this.LoggedOut();
      }
    });
   
    this.addressianAutoCompleteLooking$ = this.autoCompleteControlLooking.valueChanges.pipe(
      startWith(""),
      // delay emits
      debounceTime(1000),
      // use switch map so as to cancel previous subscribed events, before creating new once
      switchMap(value => {
        if (value !== "") {
          this.lookup(this.listingBuyer.Lookingpostcode).subscribe(data => {
            this.data = data;
          });

          return this.lookup(this.listingBuyer.Lookingpostcode);
        } else {
          return of(null);
        }
      })
    );

    // The auto population of github method
    this.addressianAutoCompleteCurrent$ = this.autoCompleteControlCurrent.valueChanges.pipe(
      startWith(""),
      // delay emits
      debounceTime(1000),
      // use switch map so as to cancel previous subscribed events, before creating new once
      switchMap(value => {
        if (value !== "") {
          this.lookup(this.listingBuyer.Currentpostcode).subscribe(data => {
            this.data = data;
          });

          return this.lookup(this.listingBuyer.Currentpostcode);
        } else {
          return of(null);
        }
      })
    );
    // End method
  }

  private LoggedIn() {
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }

  newCustomer(): void {
    this.submitted = false;
    this.listingBuyer = new listingBuyer();
  }

  
  emailFormControl = new FormControl("", [
    Validators.required,
    Validators.email
  ]);

  lastnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  firstnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  AddressFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  TownFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  stateFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  postcodeFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  addressnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(6)
  ]);

  lookup(value): Observable<any> {
    return this.postcodeService.search(value);
  }

  getPost(value) {
    this.listingBuyer.CurrentAddress = value.address;
    this.listingBuyer.CurrentTown = value.citytown;
    this.listingBuyer.Currentstate = value.county;
    this.listingBuyer.Currentpostcode = value.postcode;
    console.log(value);
  }

  getPosts(value) {
    this.listingBuyer.LookingTown = value.citytown;
    this.listingBuyer.Lookingstate = value.county;
    this.listingBuyer.Lookingpostcode = value.postcode;
    this.listingBuyer.LookingStreetname = value.address[2];
    console.log(value);
  }

  selectTab(nextIndex: number, presentIndex: number): void {

  
    if (presentIndex == 0) {    
      if (this.listingBuyer.title == null || this.listingBuyer.Firstname == null || this.listingBuyer.Lastname == null || this.listingBuyer.DOB == null || this.listingBuyer.phonenumber == null || this.listingBuyer.ChainStatus == null) {
        console.log("New Error");
        this.openAlertDialog() 

      }
      else 
      {
        this.selectedIndex = nextIndex;

      }

    } 
    else if (presentIndex == 1) {
      if(nextIndex > presentIndex) {
         if (this.listingBuyer.Currentpostcode == null || this.listingBuyer.CurrentAddress == null || this.listingBuyer.Currentstate == null || this.listingBuyer.CurrentTown == null || this.listingBuyer.Lookingpostcode == null || this.listingBuyer.LookingTown == null || this.listingBuyer.Lookingstate == null || this.listingBuyer.FinancialPosition == null || this.listingBuyer.SearchRadius == null || this.listingBuyer.PropertyType == null || this.listingBuyer.MinAmount == null || this.listingBuyer.MaxAmount == null || this.listingBuyer.Validity == null || this.listingBuyer.Conditions == null ) {
        this.openAlertDialog() 
      }
      else {
        this.selectedIndex = nextIndex;
      }

      } else {
        this.selectedIndex = nextIndex;
      }     
    } 
    else if (presentIndex == 2) {
     

      if(nextIndex > presentIndex) {
        if (this.listingBuyer.Type == null || this.listingBuyer.Position == null  || this.listingBuyer.otherInfo == null) {
       this.openAlertDialog() 
     }
     else {
       this.onSubmit()
     }

     } else {
      this.selectedIndex = nextIndex;
     } 
    }

  
  }
  openAlertDialog() {
    const dialogRef = this.dialog.open(AltertFormDialogComponent,{
      data:{
        message: 'HelloWorld',
        buttonText: {
          cancel: 'Done'
        }
      },
    });
  }
  onSubmit() {
    this.stateService.listingBuyer = this.listingBuyer;
    this.router.navigate(["confirmbuyerdetail"]);
  }
}
