import { Injectable } from '@angular/core';
import {
  AngularFirestore,
  AngularFirestoreCollection
} from "@angular/fire/firestore";

@Injectable({
  providedIn: 'root'
})
export class MyMatchesService {
  customersRef: any;
  propertiesRef: any;
  constructor(private db: AngularFirestore) { 

 
   
    
    this.propertiesRef = db.collection("matchesBuyer");

    this.customersRef = db.collection("matchesSeller");
  } 
 

  getMatchesSellerProperties(uid): any {
    return this.propertiesRef
    .doc(uid)
      .collection("matches")
      .ref.get();
  }
   getMatchesBuyerProperties(uid): any {
    return this.customersRef
    .doc(uid)
    .collection("matches")
    .ref.get();
  }
}