import { Injectable, EventEmitter } from "@angular/core";
import {
  AngularFirestore,
  AngularFirestoreCollection
} from "@angular/fire/firestore";
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA
} from "@angular/material/dialog";
import { matchesSeller } from "../../../../../../Model/matchesSeller";
import { matchesBuyer } from "../../../../../../Model/matchesBuyer";
@Injectable({
  providedIn: "root"
})
export class SellerSelectedPropertyDetailService {
  customersRef: any;
  propertiesRef: any;
  return: any;
  private dbPath = "matchesSeller";
  private dbPathBuyer = "matchesBuyer";
  dataSavedEventEmitter = new EventEmitter<string>();

  matcheBuyerRef: AngularFirestoreCollection<matchesSeller> = null;
  matcheSellerRef: AngularFirestoreCollection<matchesBuyer> = null;
  constructor(private db: AngularFirestore) {
    this.matcheSellerRef = db.collection(this.dbPath);
    this.matcheBuyerRef = db.collection(this.dbPathBuyer);

    //Buyer Database
    this.customersRef = db.collection("listingBuyer");
  }

  //create Database Seller Matches
  async matchesSellerCreate(key, customer: matchesSeller) {
    this.return = await this.matcheBuyerRef
      .doc(key)
      .collection("matches")
      .add({ ...customer })
      .then(function(data) {
        console.log("Document successfully written!");
      });
    return true;
  }

  async matchesBuyerCreate(key, customer: matchesBuyer) {
    this.return = await this.matcheSellerRef
      .doc(key)
      .collection("matches")
      .add({ ...customer })
      .then(function(data) {
        console.log("Document successfully written!");
      });
    return true;
  }
}
